import Header from './zuoye-1/Header';
import Home from './zuoye-1/Home';
import Doc from './zuoye-1/Doc';
import Tuto from './zuoye-1/Tuto';
import Blog from './zuoye-1/Blog';
import Comm from './zuoye-1/Comm';
import Login from './zuoye-1/Login';

// import {Core,Guide,Api,Hooks} from './zuoye-1/Doc';
import Core from './zuoye-1/Core';
import Guide from './zuoye-1/Guide';
import Api from './zuoye-1/Api';
import Hooks from './zuoye-1/Hooks';


const routes = [
    {       
        path:'/',
        exact:true,
        component:Home
    },
    {       
        path:'/doc',
        component:Doc,
        pri:true,
        routes:[
            {
                path:'/doc/core',
                component:Core,
            },
            {
                path:'/doc/guide',
                component:Guide,
            },
            {
                path:'/doc/api',
                component:Api,
            },
            {
                path:'/doc/hooks',
                component:Hooks,
            },
        ]
    },
    {       
        path:'/tuto',
        component:Tuto,
        pri:true
    },
    {       
        path:'/blog',
        component:Blog
    },
    {       
        path:'/comm',
        component:Comm
    },
    {       
        path:'/login',
        component:Login
    },
]

export default routes;








